.example {
  color: #2f2f37;
  max-width: 960px;
  background-color: #fff;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.example code {
  color: #fff;
  background-color: #ff6b0a;
  border-radius: 2px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
}

.example .background {
  height: 240px;
  background-image: url("logo.268195b3.png");
  background-position: center;
  background-size: cover;
}

.contact-form {
  width: 600px;
  flex-direction: column;
  margin: 0 auto;
  padding: 40px;
  display: flex;
  box-shadow: 0 10px 10px #ccc;
}

.contact-form-input {
  height: 40px;
  border: 1px solid #303030;
  margin-bottom: 20px;
  padding-left: 15px;
}

.contact-form-message {
  height: 120px;
  resize: none;
  border: 1px solid #303030;
  margin-bottom: 20px;
  padding: 15px;
}

.contact-form-submit {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #303030;
  border-radius: 4px;
  align-self: center;
  padding: 10px 20px;
  transition: transform .25s linear;
}

.contact-form-submit:hover, .contact-form-submit:focus {
  transform: scale(1.05);
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

